<template>
	<view class="search">
		<musichead title="搜索" :icon="true" :iconblack="true"></musichead>
		<view class="container">
			<scroll-view scroll-y="true">
				<!-- 搜索框 -->
				<view class="search-search">
					<text class="iconfont icon-fangdajing"></text>
					<input type="text" placeholder="搜索歌曲" v-model="searchWord" @confirm="handleToSearch(searchWord)" @input="handleToSuggest"/>
					<text v-show="searchType != 1" class="iconfont icon-guanbi" @tap="handleToClose"></text>
				</view>
				<block v-if="searchType == 1">
					<!--搜索记录-->
					<view class="search-history">
						<view class="search-history-head">
							<text>历史记录</text>
							<text class="iconfont icon-lajitong" @tap="handleToClear"></text>
						</view>
						<view class="search-history-list">
							<view v-for="(item, index) in searchHistory" :key="index" @tap="handleToWord(item)">{{ item }}</view>
						</view>
					</view>
					<!--热搜榜-->
					<view class="search-hot">
						<view class="search-hot-head">热搜榜</view>
						<view class="search-hot-item" v-for="(item, index) in searchHot" :key="index" @tap="handleToWord(item.searchWord)">
							<view class="search-hot-top">{{ index + 1 }}</view>
							<view class="search-hot-word">
								<view>
									{{ item.searchWord }}<image :src="item.iconUrl" mode="aspectFit"></image>
								</view>
								<view>{{ item.content }}</view>
							</view>
							<text class="search-hot-count">{{ item.score }}</text>
						</view>
					</view>
				</block>
				<block v-else-if="searchType == 2">
					<view class="search-result">
						<view class="search-result-item" v-for="(item, index) in searchList" :key="index" @tap="handleToDetail(item.id)">
							<view class="search-result-word">
								<view>{{ item.name }}</view>
								<view>{{ item.artists[0].name }} - {{ item.album.name }}</view>
							</view>
							<text class="iconfont icon-play"></text>
						</view>
					</view>
				</block>
				<block v-else-if="searchType == 3">
					<view class="search-suggest">
						<view class="search-suggest-head">搜索“{{ searchWord }}”</view>
						<view class="search-suggest-item" v-for="(item, index) in searchSuggest" :key="index" @tap="handleToWord(item.keyword)">
							<text class="iconfont icon-fangdajing"></text>{{ item.keyword }}
						</view>
					</view>
				</block>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import '@/common/iconfont.css'
	import musichead from '../../components/musicHead/musicHead.vue'
	import {searchHot, searchWord, searchSuggest} from '../../common/api.js'
	export default {
		data() {
			return {
				data: '',
				searchHot: [],
				searchWord: '',
				searchHistory: [],
				searchType: 1,
				searchList : [],
				searchSuggest: []
			}
		},
		onLoad() {
			searchHot().then((res)=>{
				if(res[1].data.code == '200'){
					this.searchHot = res[1].data.data
				}
			})
			// 取出历史记录, 刷新了也保持历史记录的值
			uni.getStorage({
				key: 'searchHistory',
				success: (res)=>{
					this.searchHistory = res.data
				}
			})
		},
		components: {
			musichead
		},
		methods: {
			handleToWord(word){
				this.searchWord = word
				this.handleToSearch(word)
			},
			// 搜索历史，保存历史记录
			handleToSearch(word){
				this.searchHistory.unshift(word)
				// 搜索内容去重
				this.searchHistory = [...new Set(this.searchHistory)];
				if(this.searchHistory.length > 10){
					this.searchHistory.length = 10
				}
				// 设置本地存储, 为了下次刷新的时候, 还能看到搜索记录
				uni.setStorage({
					key: 'searchHistory',
					data: this.searchHistory
				})
				this.getSearchList(word)
			},
			handleToClear(){
				uni.removeStorage({
				    key: 'searchHistory',
				    success: (res)=>{
				        this.searchHistory = []
				    }
				});
			},
			// 获取搜索结果
			getSearchList(word){
				searchWord(word).then((res)=>{
					// console.log(res)
					if(res[1].data.code == '200'){
						this.searchList = res[1].data.result.songs;
						this.searchType = 2;
					}
				});
			},
			handleToClose(){
				this.searchWord = ''
				this.searchType = 1
			},
			handleToDetail(songId){
				uni.navigateTo({
					url: '/pages/detail/detail?songId=' + songId
				});
			},
			// 边输入边搜索
			handleToSuggest(ev){
				// 获取输入内容
				let value = ev.detail.value;
				// console.log(value)
				// 判断输入内容为空的时候回到起始位置
				if(!value){
					this.searchType = 1;
					return;
				}
				// 不为空则继续向下搜索
				searchSuggest(value).then((res)=>{
					// console.log(res)
					// 判断res接口的状态
					if(res[1].data.code == '200'){
						this.searchSuggest = res[1].data.result.allMatch;
						this.searchType = 3;
					}
				});
			}
		}
	}
</script>

<style scoped>
	/*搜索栏*/
	.search-search{
		display: flex;
		align-items: center;
		height: 70rpx;
		margin: 70rpx 30rpx 50rpx 30rpx;
		background: #f7f7f7;
		border-radius: 50rpx;
	}
	.search-search text{
		margin: 0 26rpx;
	}
	.search-search input{
		flex: 1;
		font-size: 26rpx;
	}
	/*搜索历史*/
	.search-history{
		margin: 0 30rpx 50rpx 30rpx;
		font-size: 26rpx;
	}
	.search-history-head{
		display: flex;
		justify-content: space-between;
		margin-bottom: 36rpx;
	}
	.search-history-list{
		display: flex;
		flex-wrap: wrap;
	}
	.search-history-list view{
		padding: 16rpx 28rpx;
		border-radius: 40rpx;
		margin-right: 30rpx;
		margin-bottom: 30rpx;
		background:#f7f7f7;
	}
	/*热搜榜*/
	.search-hot{
		margin:40rpx 30rpx; 
		font-size:26rpx; 
	}
	.search-hot-head{
		margin-bottom: 36rpx;
	}
	.search-hot-item{
		display: flex;
		align-items: center;
		margin-bottom: 58rpx;
	}
	.search-hot-top{
		width:60rpx; 
		color:#fb2221; 
		font-size:34rpx;
		margin-left: 8rpx;
	}
	.search-hot-word{
		flex:1;
	}
	.search-hot-word view:nth-child(1){
		font-size: 30rpx;
		color: black;
	}
	.search-hot-word view:nth-child(2){
		font-size: 24rpx;
		color: #878787;
	}
	.search-hot-word image{
		width: 48rpx;
		height: 22rpx;
	}
	.search-hot-count{
		color: #878787;
	}
	
	.search-result{
		border-top: 2rpx #e4e4e4 solid;
		padding: 30rpx;
	}
	.search-result-item{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 30rpx;
		margin-bottom: 30rpx;
		border-bottom: 2rpx #e4e4e4 solid;
	}
	.search-result-word{
		
	}
	.search-result-word view:nth-child(1){
		font-size:28rpx; 
		color:#3e6694;
		margin-bottom: 12rpx;
	}
	.search-result-word view:nth-child(2){font-size:22rpx; color: #898989;}
	.search-result-item text{
		font-size:50rpx; 
	}
	/*搜索提示*/
	.search-suggest{
		border-top: 2rpx #e5e5e5 solid; 
		padding:30rpx; 
		font-size:26rpx;
	}
	.search-suggest-head{
		color:#537caa; 
		margin-bottom: 40rpx;
	}
	.search-suggest-item{
		color:#666666; 
		margin-bottom: 70rpx;
	}
	.search-suggest-item text{
		color:#c2c2c2; 
		font-size:26rpx; 
		margin-right:26rpx;
	}
</style>
